<template>
  <div class="markdown-body" v-html="htmlContent"></div>
</template>
<script>
import { marked } from 'marked'
import  markedKatex  from 'marked-katex-extension'
import hljs from 'highlight.js'
import "../assets/css/gitub-markdown.css";
import 'highlight.js/styles/github-dark.css';
import "../assets/css/katex.css";
hljs.registerLanguage('java', require('highlight.js/lib/languages/java'))
hljs.registerLanguage('python', require('highlight.js/lib/languages/python'))
hljs.registerLanguage('javascript', require('highlight.js/lib/languages/javascript'))
hljs.registerLanguage('html', require('highlight.js/lib/languages/xml'))
hljs.registerLanguage('css', require('highlight.js/lib/languages/css'))
// 其他语言依此类推
marked.use(markedKatex({
  output: 'mathml',   // 👈 改为只输出 MathML
  strict: false,
  throwOnError: false
}))
marked.setOptions({
  highlight: (code, lang) => {
    if (!lang) return hljs.highlight(code, { language: 'plaintext' }).value
    try {
      return hljs.highlight(code, { language: lang }).value
    } catch (e) {
      console.warn(`Highlight.js 无法识别语言：${lang}，已自动降级为 plaintext`)
      return hljs.highlight(code, { language: 'plaintext' }).value
    }
  },
  gfm: true,
  breaks: false,
  pedantic: false,
  langPrefix: 'language-' // 必须设置，否则 class 名不对
})
export default {
  name: 'MarkdownRender',
  props: {
    content: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      htmlContent: '',
    };
  },
  computed: {
    // 这里也可以用 computed 来替代 setup 中的 marked 处理
  },
  watch: {
    content: {
      immediate: true,
      handler(newVal) {
        this.parseMarkdown(newVal);
      },
    },
  },
  mounted() {
    this.parseMarkdown(this.content);
    this.$nextTick(() => {
      this.highlightCode();
    });
  },
  methods: {
    parseMarkdown(content) {
      this.htmlContent = marked.parse(content);
      this.$nextTick(() => {
        this.highlightCode();
      });
    },
    highlightCode() {
      document.querySelectorAll('pre code').forEach((block) => {
        hljs.highlightElement(block);
      });
    },
  },
};
</script>

<style scoped>
/* 保持与之前一致即可 */
</style>